body {
  background-color: #f5f5f5;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 8px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
}

.search-form {
  margin-bottom: 20px;
}

input[type="text"] {
  width: 70%;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
}

button[type="submit"], .load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

